export default {
  name: 'Header',
  data() {
    return {
      dropdownMenuDisplayed: false,
      navDisplayed: true      
    }
  },
  methods: {
    displayMenu() {
      this.dropdownMenuDisplayed = !this.dropdownMenuDisplayed;
    }
  }
}



