import * as d3 from 'd3';

import Utility from '@/utilities/Utility.js'

export default {
  name: '',
  data() {
    return {data: '', data_process: '', popularTimes: ''};
  },
  mounted() {
    this.load_data(this)
  },
  methods: {
    load_data(context) {
      var w_wrap = 27,
        h_wrap = 24;

      var sorter = {
        // "sunday": 0,  << if sunday is first day of week
        "monday": 1,
        "tuesday": 2,
        "wednesday": 3,
        "thursday": 4,
        "friday": 5,
        "saturday": 6,
        "sunday": 7
      }

      // Unicode conversion. (Need it because D3.json() load unicode, but d3.text() not).
      var convertunicode = function(str) {
        return str.replace(/\\u[\dA-F]{4}/gi, function(match) {
          return String.fromCharCode(parseInt(match.replace(/\\u/g, ''), 16));
        });
      };

      // Brute force Flatten with indexes to simplify D3 processing.
      var preprocess = function() {
        context.data_process = [];
        // Not pretty ...
        var i,
          j;
        for (i = 0; i < w_wrap; i++) {
          for (j = 0; j < h_wrap; j++) {
            if (!!context.data[j][i]) {
              var s = context.popularTimes.filter(function(o) {
                return o.name == convertunicode(context.data[j][i]);
              })[0];

              if (!!s) {
                s["i"] = i;
                s["j"] = j;

                // Sort by days.
                s.populartimes.sort(function sortByDay(a, b) {
                  var day1 = a.name.toLowerCase();
                  var day2 = b.name.toLowerCase();
                  return sorter[day1] > sorter[day2];
                });

                s["flat_data"] = []
                var elem,
                  elem2;
                for (elem = 0; elem < s.populartimes.length; elem++) {
                  for (elem2 = 0; elem2 < s.populartimes[elem].data.length; elem2++) {
                    s["flat_data"].push({"value": s.populartimes[elem].data[elem2], "i": elem, "j": elem2})
                  }
                }

                context.data_process.push(s);
              }
            }
          }
        }
      };

      d3.text("/static/data/metro_matrix.csv", function(d) {
        context.data = d3.csvParseRows(d)

        d3.json("/static/data/pt_metro.json", function(d) {
          context.popularTimes = d;
          preprocess();
          context.generateMap(context)
        });
      });
    },
    generateMap(context) {
      // Color scale.
      var colors = d3.scaleLinear().domain([0, 100]).range(["#EEE", "red"]);

      // All heights and widths.
      var square = 5,
        ratio = 0.3,
        w_wrap = 27,
        h_wrap = 24,
        w = 24 * ratio * square * w_wrap,
        h = 7 * square * h_wrap;

      // create the svg.
      var svg = d3.select('#grid').append('svg').attr('id', "svg").attr('width', w).attr('height', h).attr('transform', 'translate(0,60)');

      // Append montreal contour image (TO DECIDE)
      svg.append("svg:image").attr('x', -9).attr('y', -12).attr('width', 500).attr('height', 540).attr("xlink:href", "/static/data/mtl.png").attr("opacity", 0.1).attr("transform", "translate(-1130,-1130) scale(5.3)");

      var load_map = function() {
        // create group for each subway.
        var subways = svg.selectAll('g').data(context.data_process).enter().append('g').attr("id", function(d) {
          return d.name;
        }).each(function(d) {
          // Keep some data from d for the heat map.
          var frame_x = d.i;
          var frame_y = d.j;
          var frame_name = d.name;

          // Heat map created at the right spot
          d3.select(this).selectAll('rect').data(d.flat_data).enter().append('rect').attr('class', function(d) {
            return 'rect_' + frame_name;
          }).attr('id', function(d) {
            return 'day-' + (
            d.i + 1) + ' hour-' + (
            d.j + 1);
          }).attr('width', ratio * square).attr('height', square).attr('x', function(d) {
            return ratio * square * (d.j + 24 * frame_x);
          }).attr('y', function(d) {
            return square * (d.i + 7 * frame_y);
          }).attr('fill', function(d) {
            return colors(d.value);
          }).attr('stroke', "#FFF").attr("stroke-width", 0.27).attr("stroke-opacity", 0.7)
        });
      };

      load_map();
    }
  }
}



